// Type definitions
interface HierarchyComponent {
  component_id: string;
  component_name: string;
  nomenclature: string;
  ship_name?: string;
  department_id?: string;
  children?: HierarchyComponent[];
}

interface ComponentNodeData {
  component_id: string;
  component_name: string;
  nomenclature: string;
  ship_name?: string;
  department_id?: string;
  level: number;
  isRoot: boolean;
}

interface ApiResponse {
  error?: boolean;
  message?: string;
  generated_sql?: string;
  result?: Array<Record<string, any>>;
  query_quality?: string;
  records_retrieved?: number;
}

interface DrishtiResponse {
  component_id?: string;
  component_name?: string;
  nomenclature?: string;
  ship_name?: string;
  department_id?: string;
  children?: HierarchyComponent[];
  error?: boolean;
  message?: string;
  detail?: string;
}

interface QueryMode {
  mode: 'api' | 'chat' | 'drishti';
  variables: Record<string, string>;
}

interface Message {
  id: number;
  content: string;
  isUser: boolean;
  timestamp: string;
  type: 'text' | 'table' | 'sql' | 'reliability' | 'mode' | 'error' | 'warning' | 'success' | 'hierarchy';
  data?: any;
}

interface ReliabilityStats {
  average: number;
  min: number;
  max: number;
  total: number;
}

interface TableData {
  data: Array<Record<string, any>>;
  title: string;
}

interface ModeData {
  mode: string;
  icon: React.ReactNode;
  color: string;
}

const API_ENDPOINTS = {
  ask: "http://127.0.0.1:8000/ask",
  chat: "http://127.0.0.1:8000/chat",
  drishti: "http://127.0.0.1:8000/components/hierarchy",
  reliability: "http://127.0.0.1:8000/reliability"
} as const;

const getQueryMode = (input: string): QueryMode => {
  const variablePattern = /@(\w+)\s*=\s*([^@]+?)(?=\s*@|\s*$)/gi;
  const matches = [...input.matchAll(variablePattern)];
  
  if (matches.length > 0) {
    const variables: Record<string, string> = {};
    matches.forEach(match => {
      variables[match[1].toUpperCase()] = match[2].trim();
    });
    
    if (variables.SHIP_NAME && variables.NOMENCLATURE) {
      return { mode: 'drishti', variables };
    }
  }
  
  // Check for SQL-like patterns for API mode
  const sqlKeywords = /\b(SELECT|INSERT|UPDATE|DELETE|CREATE|DROP|ALTER)\b/i;
  if (sqlKeywords.test(input)) {
    return { mode: 'api', variables: {} };
  }
  
  // Default to chat mode for natural language
  return { mode: 'chat', variables: {} };
};

// Custom Component Node for React Flow
const ComponentNode: React.FC<NodeProps<ComponentNodeData>> = ({ data, selected }) => {
  const [isHovered, setIsHovered] = useState(false);
  
  const getNodeIcon = (componentName: string) => {
    const name = componentName.toLowerCase();
    if (name.includes('pump')) return <Wrench className="h-4 w-4" />;
    if (name.includes('motor')) return <Cpu className="h-4 w-4" />;
    if (name.includes('turbine')) return <Settings className="h-4 w-4" />;
    return <Settings className="h-4 w-4" />;
  };

  const getNodeColor = (level: number, isRoot: boolean) => {
    if (isRoot) return 'from-blue-500 to-blue-600';
    if (level === 1) return 'from-green-500 to-green-600';
    if (level === 2) return 'from-purple-500 to-purple-600';
    return 'from-gray-500 to-gray-600';
  };

  return (
    <div 
      className="relative"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <Handle type="target" position={Position.Top} />
      
      <div className={`
        px-4 py-3 rounded-lg shadow-lg border-2 transition-all duration-200
        ${selected ? 'border-blue-400 shadow-blue-200' : 'border-gray-200'}
        ${isHovered ? 'scale-105 shadow-xl' : ''}
        bg-gradient-to-br ${getNodeColor(data.level, data.isRoot)} text-white
        min-w-[140px] max-w-[200px]
      `}>
        <div className="flex items-center gap-2 mb-1">
          {getNodeIcon(data.component_name)}
          <div className="font-semibold text-sm truncate">
            {data.component_name}
          </div>
        </div>
        
        <div className="text-xs opacity-90">
          {data.nomenclature}
        </div>
      </div>
      
      {/* Hover tooltip */}
      {isHovered && (
        <div className="absolute z-50 bottom-full mb-2 left-1/2 transform -translate-x-1/2 
                      bg-gray-900 text-white p-3 rounded-lg shadow-xl border border-gray-700
                      min-w-[200px] max-w-[300px]">
          <div className="text-sm font-semibold mb-2 text-blue-400">Component Details</div>
          <div className="space-y-1 text-xs">
            <div><span className="text-gray-400">Name:</span> {data.component_name}</div>
            <div><span className="text-gray-400">Nomenclature:</span> {data.nomenclature}</div>
            <div><span className="text-gray-400">ID:</span> {data.component_id}</div>
            {data.ship_name && <div><span className="text-gray-400">Ship:</span> {data.ship_name}</div>}
            {data.department_id && <div><span className="text-gray-400">Department:</span> {data.department_id}</div>}
            <div><span className="text-gray-400">Level:</span> {data.level}</div>
          </div>
          
          {/* Arrow pointing down */}
          <div className="absolute top-full left-1/2 transform -translate-x-1/2 
                        border-l-8 border-r-8 border-t-8 border-transparent border-t-gray-900"></div>
        </div>
      )}
      
      <Handle type="source" position={Position.Bottom} />
    </div>
  );
};

// Function to convert hierarchy to React Flow nodes and edges
const convertHierarchyToFlow = (hierarchy: HierarchyComponent) => {
  const nodes: Node<ComponentNodeData>[] = [];
  const edges: Edge[] = [];
  
  const processNode = (component: HierarchyComponent, x: number, y: number, level: number, isRoot: boolean = false) => {
    const nodeData: ComponentNodeData = {
      component_id: component.component_id,
      component_name: component.component_name,
      nomenclature: component.nomenclature,
      ship_name: component.ship_name,
      department_id: component.department_id,
      level,
      isRoot
    };
    
    nodes.push({
      id: component.component_id,
      type: 'component',
      position: { x, y },
      data: nodeData,
    });
    
    // Process children
    if (component.children && component.children.length > 0) {
      const childrenCount = component.children.length;
      const childSpacing = 220; // Horizontal spacing between children
      const startX = x - ((childrenCount - 1) * childSpacing) / 2;
      
      component.children.forEach((child, index) => {
        const childX = startX + (index * childSpacing);
        const childY = y + 150; // Vertical spacing between levels
        
        // Create edge from parent to child
        edges.push({
          id: `${component.component_id}-${child.component_id}`,
          source: component.component_id,
          target: child.component_id,
          type: 'smoothstep',
          animated: true,
          style: { stroke: '#64748b', strokeWidth: 2 },
          markerEnd: {
            type: MarkerType.ArrowClosed,
            color: '#64748b',
          },
        });
        
        processNode(child, childX, childY, level + 1);
      });
    }
  };
  
  // Start processing from root
  processNode(hierarchy, 0, 0, 0, true);
  
  return { nodes, edges };
};

// Component Hierarchy Visualization
interface HierarchyVisualizationProps {
  hierarchy: HierarchyComponent;
}

const HierarchyVisualization: React.FC<HierarchyVisualizationProps> = ({ hierarchy }) => {
  const { nodes: initialNodes, edges: initialEdges } = convertHierarchyToFlow(hierarchy);
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);

  const nodeTypes = {
    component: ComponentNode,
  };

  return (
    <Card className="mt-4 h-[600px]">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Eye className="h-5 w-5" />
          Component Hierarchy - {hierarchy.ship_name}
        </CardTitle>
      </CardHeader>
      <CardContent className="h-[500px] p-0">
        <ReactFlow
          nodes={nodes}
          edges={edges}
          onNodesChange={onNodesChange}
          onEdgesChange={onEdgesChange}
          nodeTypes={nodeTypes}
          fitView
          fitViewOptions={{ padding: 0.2 }}
          defaultViewport={{ x: 0, y: 0, zoom: 0.8 }}
          className="bg-gray-50"
        >
          <Background color="#e2e8f0" gap={20} />
          <Controls 
            className="bg-white border border-gray-200 rounded-lg shadow-lg"
            showInteractive={false}
          />
        </ReactFlow>
      </CardContent>
    </Card>
  );
};

